import React, { useRef, useEffect } from "react";

import { Container } from "reactstrap";
import logo from "../../assets/images/res-logo.png";
import { NavLink, Link } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import icon__phone from "../../assets/images/phone__icon-header.png";
import icon__clock from "../../assets/images/clock__logo.png";
import icon__cart from "../../assets/images/icon__cart.png";
import { cartUiActions } from "../../store/shopping-cart/cartUiSlice";

import "../../styles/header.css";

const nav__links = [
  {
    display: "Главная",
    path: "/home",
  },
  {
    display: "Доставка",
    path: "/foods",
  },
  {
    display: "О нас",
    path: "/cart",
  },
  {
    display: "Заказать в телеграмме",
    path: "/contact",
  },
];

const Header = ({phone}) => {
  const menuRef = useRef(null);
  const headerRef = useRef(null);
  const totalQuantity = useSelector((state) => state.cart.totalQuantity);
  const dispatch = useDispatch();

  const toggleMenu = () => menuRef.current.classList.toggle("show__menu");

  const toggleCart = () => {
    dispatch(cartUiActions.toggle());
  };
  const href = `tel:${996776057444}`;
  useEffect(() => {
    window.addEventListener("scroll", () => {
      if (
        document.body.scrollTop > 80 ||
        document.documentElement.scrollTop > 80
      ) {
        headerRef.current.classList.add("header__shrink");
      } else {
        headerRef.current.classList.remove("header__shrink");
      }
    });

    return () => window.removeEventListener("scroll");
  }, []);

  return (
    <Container>
      <header className="header" ref={headerRef}>
        <div className="nav__wrapper d-flex align-items-center justify-content-between">
          {/* ======= menu ======= */}
          <div className="navigation" ref={menuRef} onClick={toggleMenu}>
            <div className="menu d-flex align-items-center gap-5">
              {nav__links.map((item, index) => (
                <NavLink
                  to={item.path}
                  key={index}
                  className={(navClass) =>
                    navClass.isActive ? "active__menu" : ""
                  }
                >
                  {item.display}
                </NavLink>
              ))}
            </div>
          </div>
          <div className="logo">
            <img src={logo} alt="logo" className="logo" />
          </div>
          {/* ======== nav right icons ========= */}
          <div className="nav__right d-flex align-items-center gap-4">
            <div className="schedule">
              <p className="schedule__link">
                <img src={icon__clock} alt="" />
                11:00-22:00
              </p>
            </div>
            <a href="tel:+996553 505 909" className="contact__phone">
              <p className="contact__title">
                <img src={icon__phone} alt="" /> 0553 505 909
              </p>
            </a>
            <span className="cart__icon" onClick={toggleCart}>
              <img src={icon__cart} alt="" />
              <span className="cart__badge">{totalQuantity}</span>
            </span>

            {/* <span className="user">
              <Link to="/login">
                <i class="ri-user-line"></i>
              </Link>
            </span> */}

            <span className="mobile__menu" onClick={toggleMenu}>
              <i class="ri-menu-line"></i>
            </span>
          </div>
        </div>
      </header>
    </Container>
  );
};

export default Header;
